import React,{useState, useEffect} from 'react';
import articleContent from "./Article-content"
import ArticleList from '../components/articlesList';
import NotFoundPage from './notFoundPage'
import CommentsList from '../components/commentsList'
import UpvotesSection from '../components/upvotesSection' 
import AadCommentForm from '../components/addCommentForm'

const ArticlesPage =({match}) => {
    const name =  match.params.name;
    const article = articleContent.find(article => article.name === name)
    const otherArticles = articleContent.filter(article => article.name !== name);

    const [articleInfo, setArticleInfo] = useState({upvotes:0,comments: []});

    useEffect(()=>{
        const fechData = async ()=>{
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body);
        } 
        fechData();
        //setArticleInfo({upvotes: Math.ceil(Math.random()*10)});
    },[name])

    if (!article) return <NotFoundPage></NotFoundPage>


    return (

        <>
            <h1>{article.title}</h1>
            <UpvotesSection  articleName = {name} upvotes = {articleInfo.upvotes} setArticleInfo ={setArticleInfo}></UpvotesSection>
            {article.content.map((paragraph,key) =>   (
                <p key={key}>{paragraph}</p>
            ))}
            <CommentsList comments={articleInfo.comments} ></CommentsList>
            <AadCommentForm articleName = {name} setArticleInfo ={setArticleInfo}/>
            <h3>Other Articles:</h3>
            <ArticleList articles={otherArticles}></ArticleList>
        </>

        )
};


export default ArticlesPage;